.periodic-table {
  display: grid;
    grid-template-columns: repeat(18,  80px);
  grid-template-rows: repeat(8, 80p);
/* 
  grid-template-columns: repeat(18,  calc(100vw/20));
  grid-template-rows: repeat(8, calc(100vw/10)); */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}